.courseProfessor {
  margin-bottom: 10rem;

  .row {
    margin-top: 3rem;
    display: flex;
    column-gap: 5rem;

    .photo {
      width: 250px;
      height: 250px;
      border-radius: 50%;
    }

    .content {
      max-width: 70%;
      display: flex;
      flex-direction: column;

      .name {
        font-weight: 900;
        color: $darkgrey;
        margin-bottom: 1.5rem;
      }

      .description {
        color: lighten($darkgrey, 10%);
        font-size: .9rem;
        margin-bottom: 2.5rem;
        line-height: 1.8;
      }
    }
  }
}